.topbar {
  position: fixed;
  padding: 4px 0px 0px 100px;
  z-index: 2;
  height: 55px;
  width: 100%;
  color: #fff;
  font-size: 20px;
  font-family: "Montserrat", sans-serif;
  box-shadow: 3px 1px 5px 0px rgba(33, 34, 44, 0.37);
  border-radius: 2px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  background: #f3904f; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #3b4371,
    #f3904f
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #3b4371,
    #f3904f
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.topbar #logoutButton {
  margin-left: 69%;
  font-size: 12px;
  height: 45px;
  background: #274472;
  border: 1px solid #274472;
  border-radius: 100px;
}

.topbar #logoutButton:hover {
  box-shadow: 1px 1px 2px 2px rgba(33, 34, 44, 0.37);
}

.modal_container {
  padding: 50px 90px 0px 10px;
}

.modal_button {
  display: block;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  margin: 10px;
  padding: 5px 100px 5px 5px;
  border-radius: 3px;
  border: 0px;
  background: none;
}

.modal_button:hover {
  background: #d6d6d6;
  cursor: pointer;
}

.account {
  display: block;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  margin-top: 20px;
  padding-left: 20px;
}

.logout {
  display: block;
  font-size: 12px;
  margin-top: 20px;
  padding-left: 20px;
}

.account_container {
  width: 300px;
}
